// If the user-access API is called without a query parameter, all types are returned in the response
export function fetchUserAccess(query) {
    const insights = window.insights;
    const queryString = query ? `?${query}` : '';
    if (insights && insights.chrome && insights.chrome.auth && insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`user-access/${queryString}`);
        });
    }
    else {
        return axios.get(`user-access/${queryString}`);
    }
}
