import { getFetchId, reportStateKey } from './reportCommon';
export const selectReportState = (state) => state[reportStateKey];
export const selectReport = (state, reportPathsType, reportType, reportQueryString) => selectReportState(state).byId.get(getFetchId(reportPathsType, reportType, reportQueryString));
export const selectReportFetchStatus = (state, reportPathsType, reportType, reportQueryString) => selectReportState(state).fetchStatus.get(getFetchId(reportPathsType, reportType, reportQueryString));
export const selectReportError = (state, reportPathsType, reportType, reportQueryString) => selectReportState(state).errors.get(getFetchId(reportPathsType, reportType, reportQueryString));
