import { getType } from 'typesafe-actions';
import { setFeatureFlags } from './featureFlagsActions';
export const defaultState = {
    hasFeatureFlags: false,
    isCostDistributionFeatureEnabled: false,
    isCostTypeFeatureEnabled: false,
    isCurrencyFeatureEnabled: false,
    isExportsFeatureEnabled: false,
    isFINsightsFeatureEnabled: false,
    isIbmFeatureEnabled: false,
    isNegativeFilteringFeatureEnabled: false,
    isOciFeatureEnabled: false,
    isPlatformCostsFeatureEnabled: false,
};
export const stateKey = 'featureFlags';
export function featureFlagsReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setFeatureFlags):
            return Object.assign(Object.assign({}, state), { hasFeatureFlags: true, isCostDistributionFeatureEnabled: action.payload.isCostDistributionFeatureEnabled, isCostTypeFeatureEnabled: action.payload.isCostTypeFeatureEnabled, isCurrencyFeatureEnabled: action.payload.isCurrencyFeatureEnabled, isExportsFeatureEnabled: action.payload.isExportsFeatureEnabled, isFINsightsFeatureEnabled: action.payload.isFINsightsFeatureEnabled, isIbmFeatureEnabled: action.payload.isIbmFeatureEnabled, isNegativeFilteringFeatureEnabled: action.payload.isNegativeFilteringFeatureEnabled, isOciFeatureEnabled: action.payload.isOciFeatureEnabled, isPlatformCostsFeatureEnabled: action.payload.isPlatformCostsFeatureEnabled });
        default:
            return state;
    }
}
